.backCenter {
    position: absolute;
    margin: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 85%;
    height: 80%;
    background-color: #acb3bf;
    border-radius: 15px;
}

.frontCenter {
    position: absolute;
    margin: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 95%;
    height: 95%;
    background-color: #e6e9ed;
    border-radius: 15px;
    display: table;
}

.botRow {
    position: fixed;
    top: 75%;
}

.quoteTop {
    font-family: Arial;
    padding: 30px 30px 20px;
    display: table-cell;
    vertical-align: middle;
}

.tweetbtn {
    position: fixed;
    left: 25%;
    background-color: #ffffff;
    padding: 5px 10px 5px 10px;
    border-radius: 5px;
    color: black;
}

.rerollbtn {
    position: fixed;
    left: 55%;
}

.quoteContainer {
    margin: auto;
    margin-top: auto;
    top: 25%;
    flex-wrap: wrap;
    max-width: 400px;
    justify-content: center;
    text-align: center;
    font-size: 25px;
    padding: 0% 0% 0% 0%;
}

.authorContainer {
    margin: auto;
    position: relative;
    justify-content: center;
    text-align: center;
    font-size: 25px;
    top: 0px;
}

.fade {
    opacity: 0;
    animation-name: fadeIn;
    animation-duration: 1s;
    animation-fill-mode: forwards;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}


@media only screen and (min-width: 768px) {

    .tweetbtn {
        font-size: 25px;
        left: 30%;
    }

    .rerollbtn {
        font-size: 25px;
        left: 60%;
    }
}

@media only screen and (min-width: 1000px) {
    .backCenter {
        width: 50%;
    }

    .tweetbtn {
        font-size: 20px;
        left: 40%;
    }

    .rerollbtn {
        font-size: 20px;
        left: 55%;
    }
}
